.searchForm{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding: 9px 10px;
}
.searchInput{
  padding-left: 30px;
  max-width: 10rem;
  background-color: transparent;
  border: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.5px;
  background-repeat: no-repeat;
  background-position: left center;
  background-size: contain;
}

.searchClean{
  cursor: pointer;
}