.smartLists {
  padding: 25px 0;
  list-style: none;
}
.smartListItem{
  display: flex;
  padding: 10px 12px;
  cursor: pointer;
}
.smartListItem p{
  margin-left: 1rem;
}
.icon{
  display: block;
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.arrow{
  margin-left: auto;
}